import { hasAnyPermission } from "@/utils/permissions";

/**
 * Admin middleware
 * DEPRECATED: Folosete permission() middleware n schimb
 * 
 * Verific dac utilizatorul are permisiuni administrative de baz
 */
export default function admin({ next, router }) {
  // Verific dac are permisiuni de admin (view users + view roles + view tenants)
  if (hasAnyPermission(['view users', 'view roles', 'view tenants'])) {
    return next();
  }
  
  return router.push({ name: "Dashboard" });
}
