import axios from 'axios';

// Interceptor pentru rspunsuri
axios.interceptors.response.use(
  (response) => {
    return response;
  },
  async (error) => {
    const originalRequest = error.config;

    // Dac primim 401 (Unauthorized) i nu am ncercat deja s refresh token-ul
    if (error.response?.status === 401 && !originalRequest._retry) {
      originalRequest._retry = true;

      try {
        // ncearc s faci refresh la token
        const refreshToken = localStorage.getItem('refresh_token');
        if (refreshToken) {
          const response = await axios.post('https://f1.atria.live/oauth/token', {
            grant_type: 'refresh_token',
            refresh_token: refreshToken,
            client_id: process.env.VUE_APP_CLIENT_ID,
            client_secret: process.env.VUE_APP_CLIENT_SECRET,
          });

          const { access_token, refresh_token: newRefreshToken } = response.data;
          
          // Salveaz noile token-uri
          localStorage.setItem('user', JSON.stringify(access_token));
          if (newRefreshToken) {
            localStorage.setItem('refresh_token', newRefreshToken);
          }

          // Rencearc request-ul original cu noul token
          originalRequest.headers.Authorization = `Bearer ${access_token}`;
          return axios(originalRequest);
        }
      } catch (refreshError) {
        // Dac refresh-ul eueaz, terge token-urile i redirecteaz
        console.error('Token refresh failed:', refreshError);
        localStorage.removeItem('user');
        localStorage.removeItem('refresh_token');
        window.location.href = '/login';
        return Promise.reject(refreshError);
      }
    }

    // Pentru erori 403 (Forbidden), terge token-urile i redirecteaz
    if (error.response?.status === 403) {
      localStorage.removeItem('user');
      localStorage.removeItem('refresh_token');
      window.location.href = '/login';
    }

    return Promise.reject(error);
  }
);

export default axios;
