import { hasAnyPermission } from "@/utils/permissions";

/**
 * Admin/Creator middleware
 * DEPRECATED: Folosete permission() middleware n schimb
 * 
 * Verific dac utilizatorul are permisiuni de creare/administrare coninut
 */
export default function adminCreator({ next, router }) {
  // Verific dac are orice permisiune de creare
  if (hasAnyPermission(['create categories', 'create items', 'create service categories', 'view users'])) {
    return next();
  }
  
  return router.push({ name: "Dashboard" });
}
