/**
 * Permission Helper
 * 
 * Sistem centralizat pentru verificarea permisiunilor utilizatorului
 */

/**
 * Obine permisiunile utilizatorului curent din localStorage
 * @returns {Array<string>}
 */
export function getUserPermissions() {
  try {
    const user = JSON.parse(localStorage.getItem("user"));
    return user?.permissions || [];
  } catch (error) {
    console.error("Error getting user permissions:", error);
    return [];
  }
}

/**
 * Verific dac utilizatorul are o permisiune specific
 * @param {string} permission - Numele permisiunii (ex: 'view users', 'create items')
 * @returns {boolean}
 */
export function hasPermission(permission) {
  const permissions = getUserPermissions();
  return permissions.includes(permission);
}

/**
 * Verific dac utilizatorul are cel puin una din permisiunile specificate
 * @param {Array<string>} permissions - Lista de permisiuni
 * @returns {boolean}
 */
export function hasAnyPermission(permissions) {
  const userPermissions = getUserPermissions();
  return permissions.some(permission => userPermissions.includes(permission));
}

/**
 * Verific dac utilizatorul are toate permisiunile specificate
 * @param {Array<string>} permissions - Lista de permisiuni
 * @returns {boolean}
 */
export function hasAllPermissions(permissions) {
  const userPermissions = getUserPermissions();
  return permissions.every(permission => userPermissions.includes(permission));
}

/**
 * Verific dac utilizatorul are un rol specific
 * DEPRECATED: Folosete hasPermission() n loc de verificri de roluri
 * @param {string} roleName
 * @returns {boolean}
 */
export function hasRole(roleName) {
  try {
    const user = JSON.parse(localStorage.getItem("user"));
    return user?.roles?.some(role => role.name === roleName) || false;
  } catch (error) {
    console.error("Error checking user role:", error);
    return false;
  }
}

/**
 * Verific dac utilizatorul are cel puin unul din rolurile specificate
 * DEPRECATED: Folosete hasAnyPermission() n loc de verificri de roluri
 * @param {Array<string>} roleNames
 * @returns {boolean}
 */
export function hasAnyRole(roleNames) {
  try {
    const user = JSON.parse(localStorage.getItem("user"));
    return user?.roles?.some(role => roleNames.includes(role.name)) || false;
  } catch (error) {
    console.error("Error checking user roles:", error);
    return false;
  }
}

// Export default pentru import convenabil
export default {
  hasPermission,
  hasAnyPermission,
  hasAllPermissions,
  hasRole,
  hasAnyRole,
  getUserPermissions,
};

